.counters {
	margin: 0;
	&__heading {
		margin-bottom: 25px; }
	&__row {
		margin: 0 -10px; }
	&__col {
		padding: 40px 10px;
		width: 50%; }
	&__btn {
		display: none; } }
.counter {
	&__numbers {
		@include f('lt', 100, 100); }
	&__description {
		max-width: 190px;
		@include f(null, 20, 24); } }
